.card {
    max-width: 614px;
    min-width: 614px;
    border-radius: 20px;
    margin: 50px;
    border: 20px;
    border: .3rem solid white;
}

.cardConteiner {
    widows: 1440px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.headerContainer {
    padding: 4%;
}

.cardHeader {
    background-color: #f5f5f5;
    height: 136px;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
}

.cardTitle {
    color: #5d8598;
    font-size: 23px;
    font-weight: bold;
    display: block; /* or inline-block */
    text-overflow: ellipsis;
    word-wrap: break-word;
    overflow: hidden;
    max-height: 3.6em;
}

.headerButton {
    border: .1rem solid #999999;
    border-radius: 5px;
    box-shadow: 1px 1px 1px 1px;
}

.proponenteText {
    font-size: 15px;
}

.priceSection {
    padding: 2%;
}

.area {
    color: #5d8598;
    font-size: 15px;
}

.municipio {
    color: #a5bfcc;
    font-size: 15px;
}

.rouanet {
    background-color: #cbe5b0;
    border-radius: 5px;
    padding: 3px;
    color: white;
    font-size: 17px;
}

.testmargin {
    margin-top: -15px;
}

.dinheiroAprovado {
    color: #5d8598;
    background-color: #d4e6ef;
    height: 32px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.dinheiroText {
    color: #5d8598;
    font-size: 21px;
}

.dinheiroTextValor {
    color: #5d8598;
    font-size: 21px;
}

.margintop {
    margin-top: -13px;
}

.margintopperc {
    margin-top: -7px;
}

.dinheiroCaptado {
    margin-top: 10px;
    background-color: #d4f0ea;
    height: 32px;
    margin-top: 14px;
}

.dinheiroPerc {
    margin-top: 12px;
    background-color: #e6d8f4;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    height: 32px;    
    top: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
}

.percMoney {
    border-top-left-radius: 0%;
    border-top-right-radius: 0%;
}

.percText {
    position: absolute;
    color: #8e6db1;
    font-size: 17px;
    left: 15px;
    margin-top: 8px;
}

.supporters {
    padding: 1px;
    background-color: #cccccc;
    color: white;
    font-weight: 12px;
    border-radius: 5px;
    display: block;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden
}

.seeAll {
    background-color: #a8cada;
    color: white;
    border: .2rem solid #7ba6bc;
    font-weight: bold;
}

.cardFooter {
    margin-top: 94px;
}

.aport {
    margin-top: 0px;
}

.footerButton {
    background-color: #d4e6ef;
    color: #698fa1;
    border: .2rem solid white;
    box-shadow: 1px 1px 1px 1px;
    font-weight: bold;
}

.footerButtonChoose {
    background-color: #d4e6ef;
    color: #698fa1;
    border: .2rem solid white;
    box-shadow: 1px 1px 1px 1px;
    font-weight: bold;
}

.progressbar-wrapper {
    position: relative;
    z-index: 1;
    margin-bottom: 21px;
}

.progressbar {
    position: absolute;
    margin-top: 12px;
    background-color: #e6d8f4;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    height: 32px;    
    top: 0;
    bottom: 0;
    left: -1px;
    z-index: -1;
    margin-top: 3px;
    width: 50%;
}

@media screen and (max-width:768px) {

    .card {
        max-width: 300px;
        min-width: 300px;
        border-radius: 20px;
        margin: 50px;
        border: 20px;
        border: .3rem solid white;
    }

    .headerButton {
        position: absolute;
        right: 5%;
        margin-top: 30%;
    }

    .rouanet {
        position: absolute;
        left: 7%;
        margin-top: -7%;
        background-color: #cbe5b0;
        border-radius: 5px;
        padding: 3px;
        color: white;
        font-size: 17px;
    }

    .dinheiroAprovado {
        color: #5d8598;
        background-color: #d4e6ef;
        height: 32px;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
    }
    
    .dinheiroText {
        color: #5d8598;
        font-size: 21px;
        display: none;
    }
    
    .dinheiroTextValor {
        position: absolute;
        left: 7%;
        margin-top: -7%;
        color: #5d8598;
        font-size: 21px;
    }

    .progressbar {
        position: absolute;
        margin-top: 12px;
        background-color: #e6d8f4;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        height: 32px;    
        top: 0;
        bottom: 0;
        left: -7px;
        z-index: -1;
        margin-top: 10px;
        width: 100%;
    }

    .percText {
        position: absolute;
        margin-top: 6%;
        left: 0;
    }

    .seeAll {
        position: absolute;
        left: 7%;
        width: 85%;
    }

    .footerButton {
        left: 15%;
        margin-top: 20px;
    }

    .footerButtonChoose {
        left: -5%;
        width: 95%;
        background-color: #d4e6ef;
        color: #698fa1;
        border: .2rem solid white;
        box-shadow: 1px 1px 1px 1px;
        font-weight: bold;
    }

}